import React from 'react';
import {makeStyles, useTheme} from "@material-ui/core";
import {inject, observer} from "mobx-react";
import {AutoSizer, List, CellMeasurer, CellMeasurerCache} from "react-virtualized";

const useStyles = makeStyles(theme => ({
    root: {
        width: '100%',
        height: '100%',
        overflow: 'hidden',
        marginRight: props => props.isMobile ? theme.spacing(0) : theme.spacing(-3),
        paddingRight: props => props.isMobile ? theme.spacing(1) : theme.spacing(3),
        paddingLeft: theme.spacing(1),
    },
}));

const cache = new CellMeasurerCache({defaultHeight: 100, fixedWidth: true});

function CellRenderer({index, key, parent, style, list}) {
    return <CellMeasurer key={key} rowIndex={index} columnIndex={0} cache={cache} parent={parent}>
        {({measure, registerChild}) => <div ref={registerChild} style={style}>
            {list[index](measure)}
        </div>}
    </CellMeasurer>
}


function VirtualList({system, children}) {
    const classes = useStyles({isMobile: system.isMobile});
    const theme = useTheme();

    return <div className={classes.root}>
        <AutoSizer>
            {({height, width}) => (
              <List
                height={height}
                rowCount={children.length}
                rowHeight={cache.rowHeight}
                deferredMeasurementCache={cache}
                rowRenderer={({index, key, parent, style}) => CellRenderer({index, key, parent, style, list: children})}
                width={width}
                style={{
                    paddingRight: theme.spacing(system.isMobile ? 1 : 3),
                    transform: `translateX(${theme.spacing(system.isMobile ? 0.5 : 1.5)}px)`,
                    outline: 'none',
                }}
              />
            )}
        </AutoSizer>
    </div>
}

export default inject('system')(observer(VirtualList));
