import React from 'react';
import clsx from 'clsx';
import {Paper, Typography, makeStyles} from "@material-ui/core";
import {inject, observer} from "mobx-react";
import demoIcon from './demo.svg';
import paperIcon from './pdf.svg';
import videoIcon from './video.svg';

// const height = 310;
const topHeight = 40;
const iconSize = topHeight * 0.7;
const useStyles = makeStyles(theme => {
    const radius = theme.spacing(1.5);
    const translate = radius * (2 - Math.sqrt(2)) / 2;
    const triSize = topHeight - translate;
    return {
        root: {
            width: `calc(100% - ${theme.spacing}px)`,
            margin: theme.spacing(3.5, 0.5),
            "&$primary": {
                backgroundColor: theme.palette.primary.main,
                cursor: 'pointer'
            },
            "&$primary:hover": {
                backgroundColor: theme.palette.secondary.light,
            },
            "&$secondary": {
                backgroundColor: theme.palette.secondary.main,
            }
        },
        topBar: {
            width: '100%',
            height: topHeight,
            display: 'flex',
            backgroundColor: theme.palette.background.paper,
            position: 'relative',
        },
        source: {
            width: '40%',
            flex: '0 0 40$',
            height: topHeight,
            borderTopLeftRadius: theme.spacing(0.5),
            borderBottomRightRadius: radius,
            "$primary &": {
                backgroundColor: theme.palette.primary.main,
            },
            "$primary:hover &": {
                backgroundColor: theme.palette.secondary.light,
            },
            "$secondary &": {
                backgroundColor: theme.palette.secondary.main,
            }
        },
        primary: {},
        secondary: {},
        sourceText: {
            width: '100%',
            textAlign: 'center',
            color: theme.palette.secondary.contrastText,
            lineHeight: `${topHeight}px`,
            fontSize: 21,
        },
        triangle: {
            width: 0,
            height: 0,
            transform: `translateX(-${translate}px)`,
            "$primary &": {
                borderTop: `${triSize}px solid ${theme.palette.primary.main}`,
            },
            "$primary:hover &": {
                borderTop: `${triSize}px solid ${theme.palette.secondary.light}`,
            },
            "$secondary &": {
                borderTop: `${triSize}px solid ${theme.palette.secondary.main}`,
            },
            borderRight: `${triSize}px solid transparent`,
        },
        title: {
            flex: 1,
            fontSize: 21,
            lineHeight: `${topHeight}px`,
            fontWeight: 'bold',
            textAlign: 'center',
        },
        btns: {
            position: 'absolute',
            top: 0,
            right: 0,
            height: topHeight,
            display: "flex",
            alignItems: 'center',
        },
        btn: {
            margin: theme.spacing(0, 1, 0, 0),
            width: iconSize,
            height: iconSize,
            cursor: "pointer"
        },
        content: {
            width: '100%',
            padding: theme.spacing(2),
            borderTopLeftRadius: theme.spacing(0.5),
            backgroundColor: theme.palette.background.paper,
            display: 'flex',
        },
        img: {
            width: '40%',
            objectFit: 'contain',
            flex: '0 0 auto',
        },
        textInfo: {
            flex: 1,
            marginLeft: topHeight,
        },
        textTitle: {
            fontSize: 18,
            fontWeight: 'bold',
            marginBottom: theme.spacing(2),
        },
        textAuthors: {
            fontSize: 18,
            marginBottom: theme.spacing(2),
        },
        textPublication: {
            fontStyle: 'italic',
            fontSize: 18,
        }
    };
});

const joinAuthors = authors => {
    if (authors.length === 1) return authors;
    return authors.slice(0, authors.length - 1).join(', ') + ', and ' + authors[authors.length - 1];
}

const joinPublication = (transaction, volume, issue, page, year) => {
    return `${transaction}, ${volume}(${issue}):${page[0]}-${page[1]}, ${year}.`
}

function Project({project, data, color = 'secondary', onClick = null, onLoad = null}) {
    const classes = useStyles();
    const {id, source, title, authors, teaser, transaction, volume, issue, page, year, demo, paper, video} = project;

    return <Paper className={clsx(classes.root, classes[color])}
                  onClick={onClick instanceof Function ? onClick : undefined}>
        <div className={classes.topBar}>
            <div className={classes.source}>
                <Typography className={classes.sourceText}>{source}</Typography>
            </div>
            <div className={classes.triangle}/>
            <Typography className={classes.title}>
                {id}
            </Typography>
            <div className={classes.btns}>
                {!!demo && <img className={classes.btn} src={demoIcon} alt={demoIcon}
                                onClick={() => window.open(demo)}/>}
                {!!paper && <img className={classes.btn} src={paperIcon} alt={paperIcon}
                                 onClick={() => data.download(paper, `${id}.pdf`)}/>}
                {!!video && <img className={classes.btn} src={videoIcon} alt={videoIcon}
                                 onClick={() => window.open(video)}/>}
            </div>
        </div>
        <div className={classes.content}>
            <img className={classes.img} alt={teaser} src={teaser}
                 onLoad={onLoad instanceof Function ? onLoad : undefined}/>
            <div className={classes.textInfo}>
                <Typography className={classes.textTitle}>{title}</Typography>
                <Typography className={classes.textAuthors}>{joinAuthors(authors)}</Typography>
                <Typography
                  className={classes.textPublication}>{joinPublication(transaction, volume, issue, page, year)}</Typography>
            </div>
        </div>
    </Paper>
}

export default inject('data')(observer(Project));
