import React from 'react';
import {makeStyles} from "@material-ui/core";
import {inject, observer} from "mobx-react";
import Section from "../CompLib/Section";
import VirtualList from "../CompLib/VirtualList";
import MobProject from "../Home/ScrollContent/MobProject";

const useStyles = makeStyles(theme => ({
    root: {
        position: 'absolute',
        top: 70,
        left: 0,
        right: 0,
        bottom: 0,
    },
}));

function MobPublications({t, data}) {
    const classes = useStyles();
    const {sortedPublications: sortedPubs} = data;
    let list = [];
    sortedPubs.forEach((year, yId) => {
        list = list.concat([() => <Section key={`y${yId}`} title={year.year}/>,
            ...year.publications
              .map((pub, pId) => {
                  return measure => <MobProject key={`p${pId}`}
                                                project={pub}
                                                color={'secondary'}
                                                onLoad={measure}/>
              })
        ])
    });

    return <div className={classes.root}>
        <VirtualList>
            {list}
        </VirtualList>
    </div>
}

export default inject('t', 'data')(observer(MobPublications));
