import React, {useEffect, useState} from 'react';
import clsx from 'clsx';
import {Paper, Button, Typography, makeStyles} from "@material-ui/core";
import {ExpandLess, ExpandMore} from "@material-ui/icons";
import {inject, observer} from "mobx-react";
import demoIcon from './demo.svg';
import paperIcon from './pdf.svg';
import videoIcon from './video.svg';

const useStyles = makeStyles(theme => ({
    root: {
        width: '100%',
        minHeight: 100,
        margin: theme.spacing(1, 0),
        padding: theme.spacing(0.5, 1, 1),
        height: 'fit-content',
        backgroundColor: theme.palette.secondary.main,
        textAlign: 'center',
    },
    source: {
        color: theme.palette.secondary.contrastText,
        fontSize: 18,
        fontWeight: 'bold',
        textAlign: 'center'
    },
    content: {
        minHeight: 100,
        margin: theme.spacing(1, 0, 0),
        height: 'fit-content',
        padding: theme.spacing(0, 1)
    },
    id: {
        fontSize: 18,
        fontWeight: 'bold',
        textAlign: 'center'
    },
    img: {
        width: '100%',
        objectFit: 'contain',
        flex: '0 0 auto',
    },
    title: {
        fontSize: 14,
        fontWeight: 'bold',
        textAlign: 'center'
    },
    authors: {
        fontSize: 14,
        textAlign: 'left'
    },
    publication: {
        fontSize: 14,
        fontStyle: "italic",
        textAlign: 'left'
    },
    actions: {
        display: 'flex',
        justifyContent: 'space-evenly',
        padding: theme.spacing(1, 0)
    },
    btn: {
        margin: theme.spacing(0, 1, 0, 0),
        width: 30,
        height: 30,
        cursor: "pointer",
        fontWeight: 'bold',
        textTransform: 'none'
    },
    abstract: {
        fontSize: 14,
        textAlign: 'justify',
    }
}));

const joinAuthors = authors => {
    if (authors.length === 1) return authors;
    return authors.slice(0, authors.length - 1).join(', ') + ', and ' + authors[authors.length - 1];
}

const joinPublication = (transaction, volume, issue, page, year) => {
    return `${transaction}, ${volume}(${issue}):${page[0]}-${page[1]}, ${year}.`
}

function MobProject({project, data, onClick = null, onLoad = null}) {
    const classes = useStyles();
    const [showAbstract, setAbstract] = useState(false);
    const {id, source, title, authors, teaser, transaction, volume, issue, page, year, demo, paper, video, abstract} = project;

    useEffect(() => {
        if (onLoad instanceof Function) onLoad();
    }, [showAbstract]);

    return <Paper className={classes.root}
                  onClick={onClick instanceof Function ? onClick : undefined}>
        <Typography className={classes.source}>{source}</Typography>
        <Paper className={classes.content}>
            <Typography className={classes.id}>{id}</Typography>
            <img className={classes.img} alt={teaser} src={teaser}
                 onLoad={onLoad instanceof Function ? onLoad : undefined}/>
            <Typography className={classes.title}>{title}</Typography>
            <Typography className={classes.authors}>{joinAuthors(authors)}</Typography>
            <Typography
              className={classes.publication}>{joinPublication(transaction, volume, issue, page, year)}</Typography>
            {!!abstract && <Button className={classes.abstractBtn}
                    endIcon={showAbstract ? <ExpandLess/> : <ExpandMore/>}
                    onClick={() => setAbstract(!showAbstract)}>
                Abstract
            </Button>}
            {showAbstract && <Typography className={classes.abstract}>{abstract}</Typography>}
            <div className={classes.actions}>
                {!!demo && <img className={classes.btn} src={demoIcon} alt={demoIcon}
                                onClick={() => window.open(demo)}/>}
                {!!paper && <img className={classes.btn} src={paperIcon} alt={paperIcon}
                                 onClick={() => data.download(paper, `${id}.pdf`)}/>}
                {!!video && <img className={classes.btn} src={videoIcon} alt={videoIcon}
                                 onClick={() => window.open(video)}/>}
            </div>
        </Paper>
    </Paper>
}

export default inject('data')(observer(MobProject));
